// @vue/component
import { GetBusinessHotelMerchantByhotel } from '../../../api/api_business_hotel_and_scale';
import { GetSystemDictDataByType } from '../../../api/api_query';
import { utilsDictType } from '../../../utils/dictType';

export default {
  name: 'Newmerchantpage',

  components: {},

  mixins: [],

  props: {},

  data () {
    return {
      hotelMerchentAll: [],
      organizationTypeObj: {},
      statusObj: { 0: '审核中', 1: '已签约', 2: '审核失败' },
      statusArray: [1,2,0],
    }
  },

  computed: {},

  watch: {},

  created () {
    this.$store.commit('setHeader', '二级商户');
    this.getType();
    this.fetchMerchantDatas();
  },

  methods: {
    fetchMerchantDatas() {
      for (let i = 0; i < this.statusArray.length; i++) {
        const obj = { status: this.statusArray[i] };
        GetBusinessHotelMerchantByhotel(this.$route.query.hotelId, obj).done((res) => {
          this.hotelMerchentAll = this.hotelMerchentAll.concat(res.data);
        });
      }
    },
    getType() {
      GetSystemDictDataByType({ dictType: 'business_wx_organization_type' }).done((res) => {
        this.organizationTypeObj = utilsDictType(res.data, 'obj');
      });
    },
    addChild(item) {
      this.$router.push({
        path: '/merchantAddOrEdit',
        query: {
          id: item ? item.id : '',
          status: item.status,
          hotelId: this.$route.query.hotelId
        }
      });
    },
  }
}



